*{
    box-sizing: border-box;
}

h1{
    transition: all 0.3s;
    text-shadow: 3px 3px 3px rgb(44, 43, 43);
}
h1:hover{
    transform: scale(1.2);
    text-shadow: 6px 6px 6px rgb(44, 43, 43);
}

.curr-div{
    -webkit-transform: translate(-50%, 50%);
    transform: translate(-50%, 50%);
    position: absolute;
    top: 5%;
    left: 50%;
    box-shadow: 4px 4px 4px 4px rgb(44, 43, 43);
    padding: 50px;
    margin: 30px;
    width: 40%;
    
    background: url(https://cdn.wallpapersafari.com/86/45/dGeyLN.jpg);
}



.first-div{
    background-color: rgb(203, 226, 247);
    padding: 30px;
    margin: 20px;
    border-radius: 16px;
    box-shadow: 4px 4px 4px 4px rgb(44, 43, 43);
    display: flex;
    justify-content: space-around;
}

.second-div{
    background-color: rgb(203, 226, 247);
    padding: 30px;
    margin: 20px;
    border-radius: 16px;
    box-shadow: 4px 4px 4px 4px rgb(44, 43, 43);
    display: flex;
    justify-content: space-around;
}

#first-select{
    box-shadow: 2px 2px 2px 2px #999;
    border-radius: 16px;
    width: 20%;
    padding: 3px 10px;
    border: none;
    outline: none;
}

#second-select{
    box-shadow: 2px 2px 2px 2px #999;
    border-radius: 16px;
    width: 20%;
    padding: 3px 10px;
    border: none;
    outline: none;
}

#first-input{
    box-shadow: 2px 2px 2px 2px #999;
    border-radius: 16px;
    width: 60%;
    padding: 3px 10px;
    border: none;
    outline: none;
}

#second-input{
    box-shadow: 2px 2px 2px 2px #999;
    border-radius: 16px;
    width: 60%;
    padding: 3px 10px;
    border: none;
    outline: none;
}